import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IViolationTypeModel,
  IViolationTypeRequest,
} from "../models/violation-types";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class ViolationTypesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IViolationTypeRequest) {
    return this.post<IResponse<IViolationTypeModel>>(
      "ViolationTypeSetup/Create",
      data
    );
  }

  update(data: IViolationTypeRequest) {
    return this.post<IResponse<IViolationTypeModel>>(
      "ViolationTypeSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IViolationTypeModel[]>>(
      "ViolationTypeSetup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IViolationTypeModel[]>>(
      "ViolationTypeSetup/GetAll"
    ).pipe(
      map((res) => {
        res.data.map((item: IViolationTypeModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("ViolationTypeSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
